import React, {useEffect, useState} from "react";
import {Card, CardActions, CardMedia, Grid, makeStyles} from "@material-ui/core";
import {Link} from "react-router-dom";
import axios from "axios";


const useStyles = makeStyles((theme) => ({
    container: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "baseline",
        marginTop: 20,
        padding: 30
    }
}))

export const ResultImg = ({data}) => {
    const styles = useStyles();
    const [post, setPosts] = useState([]);

    useEffect(() => {
        const dataJSON = JSON.stringify({
            // "image": data.files,
            "gen_type": data.categorySelector.value,
            "params": "color=yellow",
            "text_fields": data.postTitle + ';' + data.postText
        })

        const config = {
            headers: {
                'Content-Type': 'application/json;charset=UTF-8'
            }
        }

        const getImageByID = async () => {
            const response = await axios.post('https://skbgen.herokuapp.com/api/v1/generator/new/',
                dataJSON,
                config);
            setPosts(response.data);
        }

        getImageByID();
    }, [])

    const imgLink = 'https://skbgen.herokuapp.com/api/v1/generator/get/?id=' + post.id;

    console.log(post);
    return (
        <>
            <Grid container className={styles.container}>
                {
                    <Grid item>
                        <Card>
                            <CardMedia
                                component="img"
                                alt="resultId"
                                height="400"
                                width='400'
                                image={imgLink}
                            />
                            <CardActions>
                                <Link to={imgLink} download>Загрузить</Link>
                            </CardActions>
                        </Card>
                    </Grid>
                }
            </Grid>
        </>
    );
}