import React from "react";
import {Form} from "./Form";
import {Input} from "./Input";
import {useForm} from "react-hook-form";
import {MainContainer} from "../conteiners/MainContainer";
import {PrimaryButton} from "./PrimaryButton";
import {useHistory} from "react-router-dom";
import {FileInput} from "./FileInput";
import {useData} from "./DataContext";
import {CategorySelector} from "./CategorySelector";


export const PostForm = () => {
    const history = useHistory()
    const {data, setValues} = useData()
    const {register, control, handleSubmit} = useForm({
        defaultValues: {
            postTitle: data.postTitle,
            postText: data.postText,
            categorySelector: data.categorySelector,
            files: data.files
        },
        mode: "onBlur"
    })
    const onSubmit = (data) => {
        console.log(data);
        setValues(data);
        history.push('/result');

    };

    return <MainContainer>
        <Form onSubmit={handleSubmit(onSubmit)}>
            <Input
                {...register("postTitle")}
                id="postTitle"
                type="text"
                label="Введите заголовок поста"
                name="postTitle"
            />

            <Input
                {...register("postText")}
                id="postText"
                type="text"
                label="Введите текст поста"
                name="postText"
            />

            <CategorySelector name="categorySelector" control={control}/>

            <FileInput name="files" control={control}></FileInput>

            <PrimaryButton>Отправить</PrimaryButton>
        </Form>
    </MainContainer>
}